import React from "react";
import "./style.css";
// import Counter from "../Counter";



function ImagesCard(props) {
    return (
        <div className="row">
            <div className="col">
                <div className="card">
                    <div className="content">
                        <ul>
                            <img alt={props.name} src={props.image} />
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default ImagesCard;